import React, { Component } from 'react';
import PropTypes from 'prop-types';

class MeasurementList extends Component {
    render(){
        const { title, modalId, measurementClicked, items } = this.props
        return (
            <div>
                <h3>{title}</h3>
                {items.map(item => (
                  <button data-toggle="modal" className={ "measurementButton col-sm-12 col-5 ms"+item.score } 
                      data-target={ "#"+modalId} 
                        onClick={measurementClicked } 
                        data-measurement={item.title}
                        data-description={item.description}>{item.title}</button>
                ))}
            </div>

        );
    }
}
MeasurementList.propType = {
    list: PropTypes.string.isRequired,
    modalId: PropTypes.string.isRequired,
    measurementClicked: PropTypes.func.isRequired,
    items : PropTypes.array.isRequired
};

export default MeasurementList